.trendingFilms {
  background-color: rgb(24, 23, 23);
  position: relative;
  justify-content: center;
  display: flex;
  align-items: center;
}

.trendingFilms img {
  box-shadow: 0 0 15px orange;
  width: 98.9vw;
  height: 50vh;
  object-fit: cover;
}
.dots-container {
  display: flex;
  position: absolute;
  top: 90%;
}

.dot {
  background-color: transparent;
  color: white;
  border-style: none;
}

.selected-dot {
  font-size: 1.5em;
}
.butLeft {
  position: absolute;
  top: 45%;
  background-color: transparent;
  border: none;
  left: 1%;
  color: white;
}

.butLeft:hover {
  font-size: 1.2em;
}

.butRight {
  position: absolute;
  top: 45%;
  background-color: transparent;
  border: none;
  right: 1%;
  color: white;
}

.butRight:hover {
  font-size: 1.2em;
}

.slide {
  opacity: 0;
  transition-duration: 1s ease;
}

.slide.active {
  opacity: 1;
  transition-duration: 1s;
  transform: scale(1.0);
}

@media only screen and (max-width: 450px) {
  .trendingFilms {
    background-color: rgb(24, 23, 23);
    position: relative;
    justify-content: center;
    display: flex;
    align-items: center;
  }

  .trendingFilms img {
    box-shadow: 0 0 15px orange;
    width: 100vw;
    height: 15vh;
  }
  .dots-container {
    display: flex;
    position: absolute;
    top: 80%;
  }

  .dot {
    background-color: transparent;
    color: white;
    border-style: none;
    font-size: 0.6em;
  }

  .selected-dot {
    font-size: 1.2em;
  }
}
