


label{
  color:white;
}

/* Full-width inputs */
.inputlogin[type=text], input[type=password] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  box-sizing: border-box;
}

/* Set a style for all buttons */
.buttonlogin {
  background-color: orange;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
}

/* Add a hover effect for buttons */
.buttonlogin:hover {
  opacity: 0.8;
} 

/* Extra style for the cancel button (red) */
#cancelbtn {
  width: auto;
  padding: 10px 18px;
  background-color: #f44336;
}

/* Center the avatar image inside this container */
.imgcontainer {
  text-align: center;
  margin: 24px 0 12px 0;
}

/* Avatar image */
img.avatar {
  width: 5%;
  border-radius: 50%;
}

/* Add padding to containers */
.container {
  width:60%;
  margin-left: 20%;
}

/* The "Forgot password" text */
span.psw {
  float: right;
  padding-top: 16px;
  color: white;
  text-decoration: none;
}

/* Change styles for span and cancel button on extra small screens */
@media screen and (max-width: 450px) {
  span.psw {
    display: block;
    float: none;
  }
  #cancelbtn {
    width: 100%;
  }
  img.avatar {
    width: 20%;
    border-radius: 50%;
  }
}